body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	background-color: rgb(125, 246, 255);
}

.wrapper {
	height: 100vh;
	display: flex;
	align-items: center;
	flex-direction: column;
}

#scheduleContainer { /* Contains Day info and the schedule */
	margin-top: 50px;
	height: auto;
	display: flex;
	flex-direction: column;
	border: 1px solid black;
    border-radius: 10px;
    background: rgba(0, 0, 0, .05);
}

#getLocWrapper {
	font-size: 2.5em;
}

#console {
	width: 100vw;
	display: none;
	padding: 10px;
	position: fixed;
	top: 0;
	background: white;
	text-align: center;
}

#otherLoc {
	min-width: 30%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

button, #otherLoc, #getLocWrapper input {
	padding: 5px 10px;
	border: 2px solid #aaa;
	border-radius: 2px;
	background: rgba(0, 0, 0, .05);
}

button {
	font-size: 1em;
	cursor: pointer;
	transition: .2s background;
}
button:hover {
	background: rgba(0, 0, 0, .1);
}
button:focus {
	border: none;
	outline: 2px solid white;
}

#inputWrapper {
	width: 100%;
	margin: 10px auto;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
}

#inputWrapper input {
	width: 40%;
	font-size: .5em;
	/* margin-right: 10px; */
}
#inputWrapper input:focus {
	outline: 2px solid white;
}

#darkSkyLogo {
	position: absolute;
	top: 20px;
	right: 20px;
	height: 50px;
	width: auto;
	border-radius: 5px;
	background-color: black;
}
#scheduleWrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-width: 610px; /* Stops the days from wrapping more than once */
}
#dayWrapper {
    padding: 10px 15px;
    border: 1px solid black;
    margin: 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-shrink: 0;
    align-items: center;
    font: 10pt Helvetica, sans-serif;
    cursor: pointer;
}

#time {
    font-size: 1.5em;
}

#temps {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    font-size: 1.2em;
}

#low {
    color: rgb(120, 120, 120);
}
#dayInfoWrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin: 10px 20px 20px 20px;
}

#left, #right {
    display: flex;
    flex-direction: column;
}

#left {
    width: 60%;
}
#right {
    width: 40%;
    margin-top: 25px;
    font-size: 2em;
}

#dow {
    font-size: 3em;
}

#temp {
    font-size: 5.5em;
}

#precipType {
    font-size: 2em;
    text-transform: capitalize;
}
body {
  background: #00eeee;
}

.preloader {
	position: absolute;
	margin-left: -55px;
	margin-top: -100px;
	height: 110px;
	width: 110px;
	left: 50%;
	top: 50%;
}

svg {
  width: 110px;
  height: 110px;
}

path {
  stroke: #9ea1a4;
  stroke-width: 0.25;
  fill: #241E20;
}

#cloud {
  position: relative;
  z-index: 2;
}

#cloud path {
  fill: #efefef;
}

#sun {
  margin-left: -10px;
  margin-top: 6px;
  opacity: 0;
  width: 60px;
  height: 60px;
  position: absolute;
  left: 45px;
  top: 15px;
  z-index: 1;
  
  -webkit-animation-name: rotate;
  
          animation-name: rotate;
  -webkit-animation-duration: 16000ms;
          animation-duration: 16000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

#sun path {
  stroke-width: 0.18;
  fill: #9ea1a4;
}


@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }

  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}


@keyframes rotate {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }

  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

/* Rain */
.rain {
  position: absolute;
  width: 70px;
  height: 70px;
  margin-top: -32px;
  margin-left: 19px;
}

.drop {
  opacity: 1;
  background: #9ea1a4;
  display: block;
  float: left;
  width: 3px;
  height: 10px;
  margin-left: 4px;
  border-radius: 0px 0px 6px 6px;
  
  -webkit-animation-name: drop;
  
          animation-name: drop;
  -webkit-animation-duration: 350ms;
          animation-duration: 350ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.drop:nth-child(1) {
  -webkit-animation-delay: -130ms;
          animation-delay: -130ms;
}

.drop:nth-child(2) {
  -webkit-animation-delay: -240ms;
          animation-delay: -240ms;
}

.drop:nth-child(3) {
  -webkit-animation-delay: -390ms;
          animation-delay: -390ms;
}

.drop:nth-child(4) {
  -webkit-animation-delay: -525ms;
          animation-delay: -525ms;
}

.drop:nth-child(5) {
  -webkit-animation-delay: -640ms;
          animation-delay: -640ms;
}

.drop:nth-child(6) {
  -webkit-animation-delay: -790ms;
          animation-delay: -790ms;
}

.drop:nth-child(7) {
  -webkit-animation-delay: -900ms;
          animation-delay: -900ms;
}

.drop:nth-child(8) {
  -webkit-animation-delay: -1050ms;
          animation-delay: -1050ms;
}

.drop:nth-child(9) {
  -webkit-animation-delay: -1130ms;
          animation-delay: -1130ms;
}

.drop:nth-child(10) {
  -webkit-animation-delay: -1300ms;
          animation-delay: -1300ms;
}


@-webkit-keyframes drop {
  50% {
    height: 45px;
    opacity: 0;
  }

  51% {
    opacity: 0;
  }

  100% {
    height: 1px;
    opacity: 0;
  }
}


@keyframes drop {
  50% {
    height: 45px;
    opacity: 0;
  }

  51% {
    opacity: 0;
  }

  100% {
    height: 1px;
    opacity: 0;
  }
}

.text {
	font-family: Helvetica, 'Helvetica Neue', sans-serif;
	letter-spacing: 1px;
	text-align: center;
	margin-left: -43px;
	font-weight: bold;
	margin-top: 20px;
	font-size: 11px;
	color: #a0a0a0;
	width: 200px;
}
#settingsWrapper {
    /* position: absolute; */
    margin-top: 20px;
    justify-self: flex-start;
    padding: 10px 20px;
    border: 1px solid black;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-shrink: 0;
    background: rgba(0, 0, 0, .05);
}

#F, #C {
    border: none;
    outline: none;
}
